/* eslint no-undef: 0 */
/* eslint arrow-parens: 0 */
import React from 'react';
import { enquireScreen } from 'enquire-js';

import Content0 from './Content0';
import Content1 from './Content1';
import Content3 from './Content3';
import Footer0 from './Footer0';

import {
  Banner00DataSource,
  Content00DataSource,
  Content10DataSource,
  Content30DataSource,
  Footer00DataSource,
} from './data.source';
import './less/antMotionStyle.less';
import { Banner0 } from './Banner0';


let isMobile;
enquireScreen((b) => {
  isMobile = b;
});

const { location = {} } = typeof window !== 'undefined' ? window : {};

export class WelcomPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isMobile,
      show: !location.port, //  dva 2.0 
    };
  }

  componentDidMount() {
    // ;
    enquireScreen((b) => {
      this.setState({ isMobile: !!b });
    });
    // dva 2.0 
    /*  dva 2.0  start */
    if (location.port) {
      //  build  200-300ms ;
      setTimeout(() => {
        this.setState({
          show: true,
        });
      }, 500);
    }
    /*  dva 2.0  end */
  }

  render() {
    const children = [
 
      <Banner0
        id="Banner0_0"
        key="Banner0_0"
        dataSource={Banner00DataSource}
        isMobile={this.state.isMobile}
      />,
      <Content0
        id="Content0_0"
        key="Content0_0"
        dataSource={Content00DataSource}
        isMobile={this.state.isMobile}
      />,
     
      <Content3
        id="Content3_0"
        key="Content3_0"
        dataSource={Content30DataSource}
        isMobile={this.state.isMobile}
      />,

      <Footer0
        id="Footer0_0"
        key="Footer0_0"
        dataSource={Footer00DataSource}
        isMobile={this.state.isMobile}
      />,
    ];
    return (
      <div
        className="templates-wrapper"
        ref={(d) => {
          this.dom = d;
        }}
      >
        {/*  dva 2.0  {children} start */}
        {this.state.show && children}
        {/*  dva 2.0  {children} end */}
      </div>
    );
  }
}
